.projectcont {
    padding: 25px;
    margin: 0 auto;
    max-width: 1090px;
    width: fit-content;
    height: min-content;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.project {
    width: 250px;
    height: 150px;

    padding: 0px;
    margin: 5px;
    border-radius: 10px;
    overflow: hidden;
}

.project .image {
    width: 250px;
    height: 150px;
    border-radius: 10px;
    overflow: hidden;

    background-size: cover;
    background-repeat: no-repeat;

    transition: filter 0.2s ease-in-out;
}

.project .text {
    width: 250px;
    padding: 10px;
    border-radius: 10px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), transparent);

    position: absolute;
    color: white;
    z-index: 3;
}

.project h2 {
    font-family: 'Poppins', 'Roboto', sans-serif;
    color: white;
    word-wrap: normal;
}

.project p {
    opacity: 0;
    transform: translateY(-100%);
    transition-property: opacity transform;
    word-wrap: normal;
    transition: 0.2s ease-in-out;
}

.project:hover .image {
    filter: brightness(.6);
}
.project:hover p {
    opacity: 1;
    transform: translateY(0);
}