.page {
    background-color: rgb(237, 237, 237);
    padding-top: 50px;

    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100vh;
}

.page section {
    padding: 25px;
    margin: 0 auto;
    max-width: 900px;
    font-size: 14pt;
}

.page .combo {
    overflow: hidden;

    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: min-content;
    gap: 10px 10px;
}

.page .combo img {
    height: 100%;
    overflow: hidden;
}